<template>
    <div class="track-item">
        <div class="track-info">
            {{track.name}}<span class="dot"></span><span class="track-artist">{{artists}}</span>
        </div>

        <div class="icons-right">
            <span class="duration">{{toHms(track.duration_ms / 1000)}}</span>
            <!--            Add saved icon here?-->
            <md-menu class="menu" md-size="auto" md-align-trigger>
                <md-button class="md-icon-button" md-menu-trigger>
                    <md-icon>more_vert</md-icon>
                </md-button>

                <md-menu-content>
                    <md-menu-item @click="songRadio()">Song radio</md-menu-item>
                    <md-menu-item>My Item 2</md-menu-item>
                    <md-menu-item>My Item 3</md-menu-item>
                </md-menu-content>
            </md-menu>
        </div>
    </div>
</template>

<script>
    import Utils from "../js/Utils";

    export default {
        name: "TrackItem",
        props: {
            //Spotify track object
            track: Object,
        },
        data() {
            return {
                artists: '',
            }
        },
        mounted() {
            this.artists = this.track.artists.map(a => a.name).join(' & ');
        },
        methods: {
            toHms: Utils.secondsToHms,
            songRadio(){
                this.$router.push(`/radio?tracks=${this.track.id}`);
            }
        }
    }
</script>

<style scoped>
    .track-item {
        display: flex;
        justify-content: space-between;
    }

    .track-info {
        padding: 15px;
        display: flex;
        justify-content: left;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: calc(100% - 100px);
    }

    .dot {
        padding: 0 5px;
    }

    .track-artist {
        opacity: 0.6;
    }

    .duration {
        padding: 15px 0;
        display: inline-block;
        opacity:0.6;
    }

    .menu {
        vertical-align: middle;
        margin-left: 5px;
    }

</style>