<template>
    <div>
        <div class="info-preview">
            <div class="radio-image" :style="`background-image: url(${image})`"></div>
            <p class="md-title">{{title}}</p>
            <p class="md-caption">
                {{title}}
            </p>
        </div>
        <track-list :key="updateTracks" :api-function="apiFunction" :track-retrieve="trackRetrieve" :options="options"/>
    </div>
</template>

<script>
    import SpotifyApi from "../js/SpotifyApi";
    import TrackList from "../components/TrackList";

    export default {
        name: "Radio",
        components: {TrackList},
        data() {
            return {
                apiFunction: null,
                trackRetrieve: null,
                options: null,
                image: '',
                title: 'Radio',
                updateTracks: 0,
            }
        },
        async mounted() {
            let tracks = [],
                artists = [],
                genres = [];
            if (this.$route.query.hasOwnProperty('tracks'))
                tracks = this.$route.query.tracks.split(',');
            if (this.$route.query.hasOwnProperty('artists'))
                artists = this.$route.query.artists.split(',');
            if (this.$route.query.hasOwnProperty('genres'))
                genres = this.$route.query.genres.split(',');

            if (tracks.length === 0 && artists.length === 0 && genres.length === 0) {
                console.log("No seed given");
                await this.$router.push('/');
                return;
            }

            let seedsLeft = 5;
            let options = {limit: 10};
            if (tracks.length > 0)
                options.seed_tracks = tracks.slice(0, seedsLeft).join(',');
            seedsLeft -= Math.min(seedsLeft, tracks.length);
            if (artists.length > 0 && seedsLeft > 0)
                options.seed_artists = artists.slice(0, seedsLeft).join(',');
            seedsLeft -= Math.min(seedsLeft, artists.length);
            if (genres.length > 0 && seedsLeft > 0)
                options.seed_genres = genres.slice(0, seedsLeft).join(',');

            this.options = options;
            this.apiFunction = options => SpotifyApi.api.getRecommendations(options);
            this.trackRetrieve = result => result.tracks;

            this.updateTracks++;

            if (tracks.length > 0) {
                let result = await SpotifyApi.api.getTrack(tracks[0]);
                this.image = result.album.images[0].url;
                this.title = result.name + ' Song Radio';
            } else if (artists.length > 0) {
                let result = await SpotifyApi.api.getArtist(artists[0]);
                this.image = result.images[0].url;
                this.title = result.name + ' Artist Radio';
            } else if (genres.length > 0) {

            }
        }
    }
</script>

<style scoped>
    .info-preview {
        text-align: center;
    }

    .radio-image {
        width: 150px;
        height: 150px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        display: block;
        margin: 30px auto;
        border-radius: 3px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
    }

    .info-preview .md-title {
        font-weight: bold;
    }
</style>