<template>
    <div v-if="album!==null">
        <div class="info-preview">
            <div class="album-image" :style="`background-image: url(${album.images[0].url})`"></div>
            <p class="md-title">{{album.name}}</p>
            <p class="md-caption">
                Album by
                <span :key="artist.id" v-for="(artist, i) in album.artists">
                    <span v-if="i !== 0"> - </span>
                    {{artist.name}}
                </span>  {{new Date(album.release_date).getFullYear()}}
            </p>
        </div>
        <track-list :initial-result="album" :api-function="apiFunction" :track-retrieve="trackRetrieve"/>
    </div>
</template>

<script>
    import SpotifyApi from "../js/SpotifyApi";
    import TrackList from "../components/TrackList";

    export default {
        name: "Album",
        components: {TrackList},
        data() {
            return {
                album: null,
                apiFunction: null,
                trackRetrieve: null,
            }
        },
        async mounted() {
            if (!this.$route.query.hasOwnProperty('id')) {
                await this.$router.push('/');
                return;
            }

            let id = this.$route.query.id;
            this.apiFunction = options => SpotifyApi.api.getAlbum(id, options);
            this.trackRetrieve = result => result.tracks.items;

            this.album = await SpotifyApi.api.getAlbum(id);
            console.log(this.album);
        }
    }
</script>

<style scoped>
    .info-preview {
        text-align: center;
    }

    .album-image {
        width: 150px;
        height: 150px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        display: block;
        margin: 30px auto;
        border-radius: 3px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
    }

    .info-preview .md-title {
        font-weight: bold;
    }
</style>