<template>
    <div class="track-list" v-if="this.apiTracks.length>0">
        <!--        <recycle-scroller-->
        <!--                :items="apiTracks"-->
        <!--                class="recycle-list"-->
        <!--                :item-size="50"-->
        <!--                key-field="id"-->
        <!--                v-slot="{ item }">-->
        <!--            <track-item class="track-item" :track="item"/>-->
        <!--        </recycle-scroller>-->
        <track-item v-for="track in apiTracks" :key="track.id" :track="track"/>
    </div>
</template>

<script>
    import TrackItem from "./TrackItem";

    export default {
        name: "TrackList",
        components: {TrackItem},
        props: {
            apiFunction: Function,
            trackRetrieve: Function,
            options: {
                type: Object,
                default() {
                    return {};
                }
            },
            initialResult: {
                type: Object,
                default() {
                    return null;
                }
            },
            tracks: Array,
        },
        data() {
            return {
                apiTracks: [],
                complete: false,
                isRetrieving: false,
            }
        },
        async mounted() {
            if (this.tracks) {
                this.apiTracks = this.tracks;
                return;
            }

            if (this.trackRetrieve === null) return;

            this.options.offset = 0;
            this.options.limit = 20;

            if (this.initialResult !== null) {
                this.apiTracks = this.trackRetrieve(this.initialResult);
                this.options.offset = this.apiTracks.length;
                this.options.limit = this.apiTracks.length;
            } else {
                this.getMore();
            }
        },
        methods: {
            async getMore() {
                if (this.tracks) return;
                if (this.complete) return;

                let result = await this.apiFunction(this.options);
                console.log("Retrieving more apiTracks", result);
                let newApiTracks = this.trackRetrieve(result);

                let filteredNewApiTracks = newApiTracks.filter(track => !this.apiTracks.map(t => t.id).includes(track.id));
                if (filteredNewApiTracks.length !== newApiTracks.length) {
                    this.complete = true;
                    console.log("Completed loading");
                }

                this.apiTracks = this.apiTracks.concat(filteredNewApiTracks);
                this.options.offset += filteredNewApiTracks.length;
            },
            async handleScroll() {
                let distanceFromBottom = document.body.scrollHeight - window.innerHeight - window.scrollY;
                if (distanceFromBottom < 150 && !this.isRetrieving) {
                    this.isRetrieving = true;
                    await this.getMore();
                    this.isRetrieving = false;
                }
            }
        },
        created() {
            window.addEventListener('scroll', this.handleScroll);
        },
        destroyed() {
            window.removeEventListener('scroll', this.handleScroll);
        }
    }
</script>

