<template>
    <div v-if="playlist!==null">
        <div class="info-preview">
            <div class="playlist-image" :style="`background-image: url(${playlist.images[0].url})`"></div>
            <p class="md-title">{{playlist.name}}</p>
            <p class="md-caption">{{playlist.description}}</p>
            <p class="md-caption">
                Playlist by {{playlist.owner.display_name}}
            </p>
        </div>
        <track-list :initial-result="playlist" :api-function="apiFunction" :track-retrieve="trackRetrieve"/>
    </div>
</template>

<script>
    import SpotifyApi from "../js/SpotifyApi";
    import TrackList from "../components/TrackList";

    export default {
        name: "Playlist",
        components: {TrackList},
        data() {
            return {
                playlist: null,
                apiFunction: null,
                trackRetrieve: null,
            }
        },
        async mounted() {
            if (!this.$route.query.hasOwnProperty('id')) {
                await this.$router.push('/');
                return;
            }

            let id = this.$route.query.id;
            this.apiFunction = options => SpotifyApi.api.getPlaylist(id, options);
            this.trackRetrieve = result => result.tracks.items.map(item => item.track);

            this.playlist = await SpotifyApi.api.getPlaylist(id);
        }
    }
</script>

<style scoped>
    .info-preview {
        text-align: center;
    }

    .playlist-image {
        width: 150px;
        height: 150px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        display: block;
        margin: 30px auto;
        border-radius: 3px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
    }

    .info-preview .md-title {
        font-weight: bold;
        margin-bottom: 0;
    }

    .info-preview .md-caption {
        max-width: 60%;
        display: block;
        margin: 10px auto;
    }
</style>